import React from 'react';
import classes from './orderbtn.module.css';
import Swal from 'sweetalert2';

export const OrderBtn = (props) => {
    const {
        isOrderEnabled,
        totalPrice,
        setTotalPrice,
        setSelectedComponents
    } = props;

    // ,    localStor  
    const handleModal = () => {
        Swal.fire({
            title: ' ',
            html:
                '<input id="name" class="swal2-input" placeholder="">' +
                '<input id="phone" class="swal2-input" placeholder="">' +
                '<input id="city" class="swal2-input" placeholder=" ">'+
                '<div class="swal2-checkbox-container">' +
                '<input style="margin-right: 10px" type="checkbox" id="paymentMethod1" class="swal2-checkbox">' +
                '<label style="color: #000" for="paymentMethod1" class="swal2-checkbox-label"> </label>' +
                '</div>' +
                '<div class="swal2-checkbox-container">' +
                '<input style="margin-right: 10px" type="checkbox" id="paymentMethod2" class="swal2-checkbox">' +
                '<label style="color: #000" for="paymentMethod2" class="swal2-checkbox-label"> </label>' +
                '</div>',
            showCancelButton: true,
            confirmButtonText: '',
            cancelButtonText: '',
            preConfirm: () => {
                const name = document.getElementById('name').value;
                const phone = document.getElementById('phone').value;
                const city = document.getElementById('city').value;
                const paymentMethod1 = document.getElementById('paymentMethod1').checked;
                const paymentMethod2 = document.getElementById('paymentMethod2').checked;

                // 
                if (!name || !phone || !city) {
                    Swal.showValidationMessage(',   ');
                    return false;
                }
        
                if (phone.length < 10) {
                    Swal.showValidationMessage('    ');
                    return false;
                }

                if (!paymentMethod1 && !paymentMethod2) {
                    Swal.showValidationMessage('  ');
                    return false;
                }

                if (paymentMethod1 && paymentMethod2) {
                    Swal.showValidationMessage('    ');
                    return false;
                }

                const randomNumber = Math.floor(Math.random() * (1000 - 10 + 1)) + 10;
                const order = {
                    name: `  #${randomNumber}`,
                    phone: phone,
                    price: totalPrice,
                    client: name,
                    city: city,
                    paymentMethod: (() => {
                        if (paymentMethod1) {
                            return ' '
                        } else if (paymentMethod2) { return ' ' }
                    })(),
                };
                //     localStorage
                const orders = JSON.parse(localStorage.getItem('orders')) || [];
                //     
                orders.push(order);
                //      localStorage
                localStorage.setItem('orders', JSON.stringify(orders));
                //       
                setSelectedComponents({});
                setTotalPrice(799);
            },
        }).then((result) => {
            if (result.value) {
                Swal.fire({
                    title: '  ',
                    text: ' ',
                    icon: 'success'
                });
            }
        });
    };

    return (
        <>
            {isOrderEnabled ? 
            ( <button className={classes.btn} onClick={handleModal}></button> ) 
            : (
                <div className={classes.message}>
                    <p>    </p>
                </div>
            )}
        </>
    )
};